body{
    font-family: 'PT Sans', sans-serif;
    font-size: 16px;
    background: #CF4647;
}
p{
    font-family: 'PT Sans', sans-serif;
    font-size: 16px;
}
h1,  h2, h3, h4{
    font-family: 'Poppins', sans-serif;
}
h1{
    color: #CF4647;
    font-weight: 700;
}
h2{
    font-weight: 600;
}
h3{
    font-weight: 400;
}
h4{
    font-weight: 300;
}
code{
    background: #eee;
    width: 100%;
    display: block;
    padding: 15px;
    border-radius: 4px;
}